import './Graph.css';
import {Line} from 'react-chartjs-2';


function Graph(props){
    var datasets = [];

    props.data.forEach(item => {
        datasets.push(
            {
                type:'line',
                label: item.name,
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: item.color,
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'round',
                pointBorderWidth: 1,
                pointHoverRadius: 1,
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: item.y,
                lineTension: 0.4
            }
        );
    });

    return(
        <div className="eds-graph">
            <div className="eds-graph-axis x">{props.axisX}</div>
            <div className="eds-graph-axis y">{props.axisY}</div>
            <Line
                data={{
                    labels: props.data[0].x,
                    datasets: [
                        ...datasets
                    ]
                }}
                options={{
                    maintainAspectRatio: false,
                    scales: {
                        yAxes: [{
                            ticks: {
                                callback: function(value, index, values) {
                                    return value+props.unitY;
                                }
                            }
                        }],
                        xAxes: [{
                            ticks: {
                                callback: function(value, index, values) {
                                    return value+props.unitX;
                                }
                            }
                        }]
                    }
                }}
            />
        </div>
    );
}

export default Graph;